@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', sans-serif;
}

.app {
  background-image: url('./assets/Sun.png');
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

.app.fog {
  background-image: url('./assets/Fog.png');
}

.app.rain {
  background-image: url('./assets/Rain.png');
}

.app.snow {
  background-image: url('./assets/Snow.png');
}

.app.cloudy {
  background-image: url('./assets/Cloudy.png');
}


main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.60));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 0px 0px 20px 20px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.1);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.65);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFF;
  font-size: 18px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 1px 1px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  padding: 15px 25px;
  color: #FFF;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.3);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);

}

.force-mobile {
  max-width: 750px;
  max-height: 1364px;
  margin: 0 auto;
  border-style:solid;
  border-color: inherit;
  border-width: 4px;
  transform: scale(1);
  transform-origin:center;
  display: block;
}
